//Menu Wrappper
.menu-list {
    width: 200px;

    .menu-item {
        margin-left: 0;
    }
    .separate {
        border-top: 1px solid rgba(22, 24, 35, 0.3);
    }
}
.menu-popper {
    padding-bottom: 8px;
}
// Menu items
.menu-item {
    width: 100%;
    justify-content: flex-start;
    border-radius: 0;
    &:hover {
        background-color: rgba(22, 24, 35, 0.03);
    }
}
//Header

.header {
    position: relative;
    height: 50px;
    margin-top: -8px;
}
.back-btn {
    width: 50px;
    height: 50px;
    background-color: transparent;
    cursor: pointer;
}
.header-title {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}
