.wrapper {
    --search-border-radius: 92px;
    --search-height: 46px;
    --search-top-spacer: 9px;
    --search-button-width: 52px;

    width: 100%;
    height: var(--default-layout-header-height, 60px);
    box-shadow: 0px 1px 1px rgb(0 0 0 / 12%);
    display: flex;
    justify-content: center;
}

.inner {
    height: 100%;
    width: var(--default-layout-width);
    display: flex;
    align-items: center;
    justify-content: space-between;
}

//actions
.more-btn {
    font-size: 1.6rem;
    margin-left: 28px;
    background-color: transparent;
    padding: 8px;
}

.actions {
    display: flex;
    align-items: center;
    .action-icon {
        font-size: 2.6rem;
        background-color: transparent;
        color: #a1a4b8;
        padding: 4px 12px;
        cursor: pointer;
    }
}

.user-avatar {
    width: 32px;
    height: 32px;
    object-fit: cover;
    border-radius: 50%;
    margin-left: 12px;
    cursor: pointer;
}
.img {
    width: 180px;
    height: 70px;
}
